import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { ButtonAi } from '../../components/ButtonAi';
import { Heart } from '../../components/Heart';
import { Menu } from '../../components/Menu';
import { PropertyOffWrapper } from '../../components/PropertyOffWrapper';
import './style.css';

export const Result = () => {
  const [news, setNews] = useState([]); //    

  useEffect(() => {
    //     
    const fetchNews = async () => {
      try {
        const response = await axios.get('http://localhost:8000/news/');
        setNews(response.data); //     
      } catch (error) {
        console.error('  ', error);
      }
    };

    fetchNews();
  }, []); //      

  return (
    <div className="result">
      <div className="div-11">
        {/*    */}
        <div className="view-20">
          {/*    */}
        </div>
        {/*     */}
        <div className="news-container">
          {news.map((item, index) => (
            <div key={index} className="news-item">
              <a href={item.link} target="_blank" rel="noopener noreferrer">
                <img src={item.imageUrl} alt="news" className="news-image" />
                <h2>{item.title.replace(/<[^>]+>/g, '')}</h2>
                <p>{item.description.replace(/<[^>]+>/g, '')}</p>
                <p>Published on: {item.pubDate}</p>
              </a>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};
